import '../world-environment/jsx-extensions';
import {useFrame, useLoader, useThree} from '@react-three/fiber';
import { Suspense, useLayoutEffect, useMemo, useRef } from 'react';
import { ImageBitmapLoader } from 'three/src/loaders/ImageBitmapLoader';
import {
  DoubleSide,
  RGBFormat,
  Vector3,
  CubeReflectionMapping,
  UVMapping,
  RepeatWrapping,
  CanvasTexture,
  Vector2,
  EllipseCurve,
  Mesh
} from 'three';
import { useSpring, animated } from '@react-spring/three';
// import {use} from '@react-three/drei'
/* eslint-disable-next-line */
export interface GlobeProps {
  imgs: string[]
}

export function Globe(props: GlobeProps) {
  return (
    <>
      {props.imgs.map(src => (
        <Suspense
          fallback={null}
          key={src}
          >
          <GlobeItem {...{src}} />
        </Suspense>
      ))}
    </>
  );
}

export default Globe;

function GlobeItem({src, size = 250}: {src: string, size?: number}) {
  const NUM_ITEM_POINTS = 16;
  const halfDim = size / 2;
  const [img] = useLoader(ImageBitmapLoader, [src], loader => {
    (loader as ImageBitmapLoader).setOptions({ imageOrientation: 'flipY' });
  });
  const [{pW, pH}] = useSpring(() => ({pW: img.width || 0, pH: 0, config: {tension: 210, friction: 40}}));
  const ref = useRef<Mesh>();
  const tRef = useRef<CanvasTexture>();
  const imgR = useRef(document.createElement('canvas'));

  const gl = useThree(state => state.gl);
  const {drawingBufferHeight, drawingBufferWidth} = useMemo(() => gl.getContext(), [gl]);
  useLayoutEffect(() => {
    const imgEl = imgR.current;
    const context = imgEl.getContext('2d');
    console.log(context, img, imgEl);
    if (!context) return;
    imgEl.width = img.width;
    imgEl.height = img.height;
    pH.start(img.height);
    context.fillStyle = ('black');
    context.fill();
    context.drawImage(img, 0, 0, imgEl.width, imgEl.height);
    if (tRef.current) tRef.current.needsUpdate = true;
    if (ref.current)ref.current.layers.set(0);
  }, [img, imgR, pH]);
  const points = useMemo(() => {

    const bigR = Math.max(drawingBufferHeight, drawingBufferWidth);
    return new EllipseCurve(0, 0, bigR - 5, bigR - 5, 0.75 * Math.PI, 1.25 * Math.PI, false, 0).getSpacedPoints(NUM_ITEM_POINTS);
    // const curve = new EllipseCurve(halfDim / 10, 0, halfDim * 1.0, halfDim * 1.5, -0.0625 * Math.PI, .0625 * Math.PI, false, 0);

    // return curve.getPoints(4);
    // return curve.getSpacedPoints(32);
    // return [[halfDim, halfDim, 20], [-halfDim, halfDim, 20], [-halfDim, -halfDim, 20], [halfDim, -halfDim, 20], [0, 0, 25]].map(p => new Vector3(...p));
  }, [drawingBufferHeight, drawingBufferWidth]);
  useFrame(() => {
    if (!ref.current) return;
    ref.current.scale.y = pH.get() / img.height;
  });
  const tMap = useMemo(() => new CanvasTexture(img), [img]);
  return (
    <mesh
      castShadow
      receiveShadow
      // args={[null, null, 4]}
      layers={0}
      ref={ref}
      rotation={[0, 0.75 * Math.PI, Math.PI]}
      >

      {/* <bufferAttribute
        args={[rotations, 3]}
        attachObject={["attributes","rotation"]}
        /> */}
      <animated.latheGeometry
        args={[points, NUM_ITEM_POINTS, Math.PI * 0.5, Math.PI * 0.5]}
        // scale={new Vector3(1,1,1)}
        />
      {/* <latheGeometry args={[points, 4, Math.PI * 0.5, Math.PI * 0.5]} /> */}

      <meshPhongMaterial
        // color="red"
        // flatShading
        // colorWrite={false}
        fog={false}
        // bumpScale={1}
        map={tMap}
        normalMap={tMap}
        shininess={0}
        side={DoubleSide}
        >
        <canvasTexture
          attachObject={["attributes", "map"]}
          image={img}
          // mapping={}
          ref={tRef}
          // wrapS={RepeatWrapping}
          // wrapT={RepeatWrapping}
          // onUpdate={self => {
          //   // eslint-disable-next-line no-debugger
          //   debugger;
          //   // console.log(self, ref, img);
          //   (self.needsUpdate = true);
          // }}
          // format={RGBFormat}
          />
      </meshPhongMaterial>
    </mesh>
  );
}
