export const asHeroFigurine = (Component: ModelComponent, useStore: UseStore<WorldState>) => {
  return (props: ModelProps) => {
    const onHeroHover = useStore(state => state.onHeroHover);
    const onLoad = useStore(state => state.setHeroCenter);
  const bind = useDrag(onHeroHover);
  const groupRef = useRef<Group>();
  const modelRef=useRef<Group>();
  const {rY} = useSpring({rY: 0});
  useFrame(({clock, camera}) => {
    rY.start(Math.atan((clock.getElapsedTime() * 0.1)) * 0.0025);
    // rY.set(Math.cos(Math.atan(clock.getElapsedTime())));
    if (typeof groupRef.current !== 'undefined') {
      groupRef.current.rotation.y += (rY.get());
    }
    if (typeof modelRef.current !== 'undefined') {
      modelRef.current.rotation.copy(camera.rotation);
    }
  });
    return (
      <group ref={modelRef}>
        <Suspense fallback={null}>
          <animated.group
            ref={groupRef}
            rotation={[0, 0, 0]}
          // rotation-y={rY.get()}
            {...{
              onPointerOver: () => onHeroHover({down: true}),
              onPointerOut: () => onHeroHover({down: false}),
          }}
            >
            <Component
              {...props}
            // {...(bind() as unknown as {onClick: (event: ThreeEvent<MouseEvent>) => void})}
              {...{
              onLoad,
            }}
              />

          </animated.group>
        </Suspense>
      </group>
    );
  };
};

export default HeroFigurine;
