import { UseStore } from 'zustand';
import { CirculatingLight } from './CirculatingLight';
import { SHADOW_SCALE } from './SHADOW_SCALE';
import { WorldState } from '@anonymous.club/types';
import { SpotLight, SpotLightHelper, Vector3 } from 'three';
import { useEffect, useRef } from 'react';
import { useFrame } from '@react-three/fiber';
import { useHelper } from '@react-three/drei';
import { useSpring, animated } from '@react-spring/three';
import { useRouteMatch } from 'react-router-dom';
function StaticLight({shouldShowBottom = false}) {
  const ref = useRef<SpotLight>();
  const [offset, api] = useSpring(() => (new Vector3(1, 1, 1)));
  // useHelper(ref, SpotLightHelper, 'red');
  useEffect(() => {
    api.start(shouldShowBottom ? {x: 0.1, y: -3, z: 1} : {x: 1, y: 1, z: 1});
  }, [shouldShowBottom, api]);
  useFrame(({camera, clock, gl}) => {
    if (!ref.current) return;
    ref.current.position.copy(camera.position);
    // if (camera.type === 'PerspectiveCamera') {
    //   ref.current.shadow.camera.fov = camera.getEffectiveFOV() + 90;
    // }
    ref.current.shadow.camera.rotation.copy(camera.rotation);
    ref.current.shadow.camera.layers.set(0);
    // if (typeof 'red' !== 'undefined') {
    // }
    // const {drawingBufferHeight} = gl.getContext();
    const d0 = camera.position.lengthSq() / (2 ** 12);
    ref.current.position.y -= d0;
    ref.current.position.x -= d0 * 0.5;
    ref.current.position.z -= d0 * 2;

    ref.current.position.x *= offset.x.get();
    ref.current.position.y *= offset.y.get();
    ref.current.position.z *= offset.z.get();
    // ref.current.position.y -= 200;
    // ref.current.position.x *= 0;
    // ref.current.shadow.camera.position.x = -drawingBufferHeight / 2;
    // ref.current.shadow.camera.updateProjectionMatrix();
    // ref.current.position.y += 50;
    // ref.current.position.y += drawingBufferHeight / 2;
    // ref.current.position.z *= Math.cos(clock.getElapsedTime() * 2);
    // ref.current.target.position.set(0, 0, -1);
    // ref.current.updateMatrixWorld();
  });

  return (
    <spotLight
      castShadow
      angle={Math.PI * 0.2125}
      decay={0.1}
      distance={0}
      intensity={2}
      layers={0}
      penumbra={0.1}
      ref={ref}
      shadow-camera-aspect={0.9}
      shadow-camera-far={10000}
      shadow-mapSize={[2048, 2048]}
      />
  );
}
export function WorldLights({ useWorldStore }: { useWorldStore: UseStore<WorldState>; }) {
  const shouldStop = useWorldStore(state => state.isHeroHovered);
  const shouldShowBottom = useWorldStore(state => state.shouldShowBottom);
  return (
    <>
      <hemisphereLight
        // color="white"
        // groundColor="lightblue"
        intensity={0.01}
        />
      <CirculatingLight
        axis={[.1, .1, 1]}
        center={[140, -40, -100]}
        // color="gold"
        decay={2}
        freq={0.22}
        intensity={1}
        layers={1}
        // shadow-bias={-0.00002}
        radius={60}
        shadow-camera-far={10000}
        shadow-mapSize-height={SHADOW_SCALE}
        shadow-mapSize-width={SHADOW_SCALE}
        />
      <CirculatingLight
        axis={[2, 0.2, 1]}
        center={[0, 10, 40]}
        color={0xfdfffc}
        decay={30}
        distance={0}
        freq={0.5}
        intensity={0.5}
        layers={1}
        offset={3.14}
        radius={400}
        shadow-bias={-0.0004}
        shadow-camera-far={10000}
        shadow-mapSize-height={SHADOW_SCALE}
        shadow-mapSize-width={SHADOW_SCALE}
        // shouldStop={shouldStop}
        />
      <spotLight
        castShadow
        angle={1.0}
        intensity={0.5}
        layers={1}
        position={[100, 200, 300]}
        shadow-bias={-0.00002}
        shadow-mapSize-height={SHADOW_SCALE}
        shadow-mapSize-width={SHADOW_SCALE}
        />
      <StaticLight shouldShowBottom={shouldShowBottom} />
      <spotLight
        castShadow
        angle={1.0}
        decay={0}
        intensity={0.5}
        layers={1}
        position={[-100, 200, 300]}
        shadow-bias={-0.00002}
        shadow-mapSize-height={SHADOW_SCALE}
        shadow-mapSize-width={SHADOW_SCALE}
        />
    </>
  );
}
