const CurrentFigure = MODELS[1];

const ClearOnHoverMaterial = ({useWorldStore, ...props}: Required<WorldStateConsumer> & JSX.IntrinsicElements['meshPhysicalMaterial']) => {
  const ref = useRef<MeshPhysicalMaterial>();
  const isHeroHovered = useWorldStore(state => 0.3 + 0.7 * ~~!state.isHeroHovered);
  const {alpha} = useSpring({alpha: isHeroHovered});
  useFrame(() => {
  // useEffect(() => {
    // console.log(alpha, isHeroHovered);
    if (typeof ref.current !== 'undefined') {
      ref.current.opacity = alpha.get();
      // ref.current.needsUpdate = true;
    }
  });
  // }, [alpha, ref, isHeroHovered]);
  return (
    <animated.meshPhysicalMaterial
      {...props}
      ref={ref}
      // transmission={alpha.get()}
      />
  );
};

function WorldBackDrop() {
  const ref = useRef<Mesh>();
  const {gl} = useThree();
  const context = gl.getContext();
  const points = useMemo(() => {
    const bigR = Math.max(context.drawingBufferHeight, context.drawingBufferWidth);
    return new EllipseCurve(1, 0, bigR, bigR, 0.5 * Math.PI, 1.5 * Math.PI, true, 0).getSpacedPoints(64);
  }, [context.drawingBufferHeight, context.drawingBufferWidth]);
  useFrame(({camera}) => {
    if (!ref.current) return;
    ref.current.rotation.copy(camera.rotation);
  });

  return (

    <mesh
      // castShadow
      receiveShadow
      layers={0}
      ref={ref}
      scale={[1.01, 1.01, 1.01]}
      >
      {/* <sphereGeometry args={[150, 64, 64]} /> */}
      <latheGeometry args={[points, 64, Math.PI * 0.5, Math.PI * 1.0]} />
      {/* <shadowMaterial color={0xffffff} /> */}
      <meshStandardMaterial

        // wireframe
        attach="material"
        // clearcoat={1}
        // clearcoatRoughness={0.99}
        color={0xababab}
        // depthTest={false}
        fog={false}
        // metalness={0.01}
        // roughness={0.99}
        // side={DoubleSide}
        />
    </mesh>
  );
}
